.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.movie-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
  padding: 20px;
}

.movie-item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%; /* Ensure it takes the full width of its container */
  aspect-ratio: 2 / 3; /* Adjust this value to match the desired aspect ratio */
}

.movie-poster {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ensure the poster covers the area without distorting its aspect ratio */
}

.movie-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  opacity: 0;
  transition: opacity 0.3s ease;
  padding: 10px; /* Adjust padding as needed */
  box-sizing: border-box; /* Include padding and border in the element's total width and height */
  overflow: hidden; /* Ensure content does not overflow */
  height: 100%; /* Match the height of the poster */
}

.movie-title {
  color: white;
  font-size: calc(1vw + 1em); /* Adjust font size based on viewport width */
  text-align: center;
  opacity: 0; /* Hide by default */
  transition: opacity 0.3s ease; /* Smooth transition */
}

.movie-details {
  display: flex;
  background-color: aliceblue;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 10px;
  opacity: 0; /* Hide by default */
  transition: opacity 0.3s ease; /* Smooth transition */
  flex-wrap: wrap; /* Allow items to wrap if necessary */
  max-width: 100%; /* Ensure the container does not overflow */
}

.rating-logo {
  width: 1em; /* Set the width to be the same as the current font size */
  height: 1em; /* Set the height to be the same as the current font size */
  max-width: 100%; /* Ensure the logo does not exceed the container's width */
  max-height: 100%; /* Ensure the logo does not exceed the container's height */
  object-fit: contain; /* Ensure the logo maintains its aspect ratio */
  margin-right: 5px; /* Add some space to the right of the logo */
}
.movie-item:hover .movie-overlay {
  opacity: 1;
}
.movie-item:hover .movie-title,
.movie-item:hover .movie-details {
  opacity: 1; /* Show on hover */
}

@media (max-width: 600px) {
  .movie-poster {
    max-width: 100%; /* Adjust the max-width to ensure 4 movies fit in a row */
  }
}

@media (max-width: 480px) {
  .movie-grid {
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Add this to your CSS file */

.search-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 0; /* Adjust margin as needed */
}

.search-input {
  width: 80%; /* Adjust width as needed */
  padding: 10px;
  border: 2px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  outline: none;
}

.search-input:focus {
  border-color: #61dafb; /* Adjust border color on focus */
}

.search-icon {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
}

/* Add this to your CSS file */

.search-button {
  padding: 10px 20px;
  border: none;
  background-color: #61dafb;
  color: white;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  margin-left: 10px; /* Add some space between the input and the button */
}

.search-button:hover {
  background-color: #40a9f4; /* Darken the button on hover */
}
